<template>
    <div id="outermain">
        <div class="container">
            <div class="row">
                <!-- <section id="maincontent" class="ten columns positionleft"> -->
                <section id="maincontent" >
                    <div class="padcontent">
                        <section class="content">
                            <div class="breadcrumb"> Home / <a></a></div>
                            <h1 class="pagetitle"></h1>
                            <div class="table_block" id="order-detail-content">
                                <table id="cart_summary">
                                    <thead>
                                        <tr>
                                            <th class="cart_product first_item"><a class="button" v-if="show" id='btn' @click='remove_from_the_cart_all()'></a></th>
                                            <th class="cart_product"></th>
                                            <th class="cart_product first_item"></th>
                                            <th class="cart_description item"></th>
                                            <th class="cart_avail item"></th>
                                            <th class="cart_unit item"></th>
                                            <th class="cart_unit item"></th>
                                            <th class="cart_total item"></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="resource in resources" class="cart_item" >
                                            <td> <input type="checkbox" v-model="num" id="payment_method_paypal" class="input-radio" name="payment_method" :value="resource"/></td>
                                            <td class="cart_product"><router-link :to="'/product-details/' + resource.id"><img :src='resource.resource_picture' style="height:40px;width:50px"></router-link></td>
                                            <td class="cart_product">
                                                <!-- {{ resource.name }} -->
                                                <router-link :to="'/product-details/' + resource.id">{{resource.name}}</router-link>
                                            </td>
                                            <td class="cart_description">
                                               {{ resource.pub_address }}       
                                            </td>
                                            
                                            <td class="cart_avail">{{ resource.community.name }}</td>
                                            <td class="cart_unit">{{ resource.intergration }}</td>
                                            <td class="cart_unit">
                                              <div v-if='resource.process === 0' ></div>
                                              <div v-if='resource.process === 1' ></div>
                                              <div v-if='resource.process === 2' ></div>
                                              <div v-if='resource.process === 3' ></div>
                                              <div v-if='resource.process === 4' ></div>
                                              <div v-if='resource.process === 5' ></div>
                                            </td>
                                            <td class="cart_total">
                       
                                                <router-link style='color:gray' :to="'/Release/' + resource.id"><span class="price" id='operation'></span></router-link>
                                                <span class="price" @click='remove_from_the_cart(resource.id)' id='operation'></span>
                                            </td>
                                        </tr>
                                    </tbody>
                            </table>
<!-- 
                            <table   v-for='resource in resources' v-if='resource.id === resRateid'>
                               <thead>
                                <tr>
                                    <th class="cart_product " ></th>
                                    <th class="cart_product"></th>
                                    <th class="cart_product "></th>
                                    <th class="cart_product "></th>
                                </tr>
                              </thead>
                              <thbody >
                                <td class="cart_avail">{{ resource.name }}</td>
                                <td class="cart_avail">{{ resource.rate }}</td>
                                <td class="cart_unit">{{ resource.rate_content }}</td>
                                <td class="cart_unit">
                                  <div v-for='respic in resource.picList'>
                                    <img :src='respic' >
                                  </div>
                                </td>
                              </thbody>
                            </table>
 -->
                          <!--           <center>
                                      <div v-for='resource in resources'>
                                        <div v-if='resource.id === resRateid'> 
                                          <p>:{{resource.name}}</p>
                                          <p>:{{resource.rate}}</p>
                                          <p>:{{resource.rate_content}}</p>
                                          <div v-for='respic in resource.picList'>
                                            <img :src='respic' style="height:260px;width320px">
                                          </div>
                                        </div>
                                      </div>
                                      </center> -->

                                    </div>
                            </div>
                            <br><br><br>
                            <button>&nbsp
                              <button>
                                  <div id="payment">
                                      <ul class="payment_methods">
                                          <li>
                                              <label for="payment_method_bacs"> </label> 
                                              <div class="payment_box">
                                                  <p></p>
                                              </div>                      
                                          </li>
                                          <li>
                                              <label for="payment_method_cheque"> </label>  
                                              <div class="payment_box">
                                                  <p>.</p>
                                              </div>                  
                                          </li>
                                          <li>
                                              <label for="payment_method_paypal"></label>
                                              <div class="payment_box">
                                                  <p></p>
                                              </div>                      
                                          </li>
                                      </ul>
                                  
                                      <div class="form-row">      
                                          <a href="#" class="button">Place order</a>
                                      </div>
                                      <div class="clear"></div>
                                  </div>
                              </button>
                          </button>
                        </section>
                     
                    </div>
                </section>
                
            </div>
        </div>
    </div>
</template>
<style>
#operation:hover{ 
color:red
}

#btn:hover{ 
background-color:red
}
.modal-scale-transition{
  transition: transform,opacity .3s ease;
}

.modal-scale-enter,
.modal-scale-leave {
    opacity: 0;
}

.modal-scale-enter {
  transform: scale(1.1);
}
.modal-scale-leave {
  transform: scale(0.8);
}
</style>
<script>
export default {
  name: 'Checkout',
  data () {
    return {
      resourceId: '',
      resources: '',
      tableData: [{
        name: '2016-05-02',
        rate: '',
        rate_content: ' 1518 ',
        picList: ''
      }],
      resRateid: '',
      isShow: false,
      sources: 0,
      resourcenNum: 0,
      num: [],
      show: false
    }
  },
  created () {
    this.getPublic()
  },
  watch: {
    num (curVal, oldVal) {
      if (curVal) {
        if (this.num.length >= 0) {
          this.show = true
          this.sources = 0
          this.resourcenNum = this.num.length
          if (this.num.length === 0) {
            this.show = false
            this.sources = 0
          } else {
            for (var i = 0; i < this.num.length; i++) {
              this.sources += this.num[i].intergration
            }
          }
        }
      }
    }
  },
  methods: {
    getPublic () {
      this.$http.get(window.baseUrl + '/api/resources/get_pub/').then((response) => {
        if (response.data.code === 200) {
          this.resources = response.data.data
        } else {
          console.log(456)
        }
      }).catch((response) => {
        console.log(55)
      })
    },
    ordered () {
      this.$http.post(window.baseUrl + '/api/order/create/', {
        'data': this.num
      }).then((response) => {
        if (response.data.code === 200) {
          if (response.data.status === 1) {
            this.getPublic()
          } else {
            alert('')
          }
        } else {
          alert('')
        }
      }).catch((response) => {
        alert('')
      })
    },
    remove_from_the_cart_all () {
      if (this.num.length === 0) {
        alert('no')
      }
      if (confirm('is delete') && this.num.length > 0) {
        var reList = []
        for (var i = 0; i < this.num.length; i++) {
          reList.push(this.num[i].id)
        }
        this.$http.post(window.baseUrl + '/api/resources/delete/', {'pk_list': reList}).then((response) => {
          if (response.data.code === 200) {
              // this.resources = this.resources.filter(t => t.id !== resid)
            this.getPublic()
          } else {
            alert('')
          }
        }).catch((response) => {
          alert('')
        })
      }
    },
    remove_from_the_cart (resid) {
      if (confirm('is delete')) {
        this.$http.post(window.baseUrl + '/api/resources/delete/', {'pk_list': [resid]}).then((response) => {
          if (response.data.code === 200) {
              // this.resources = this.resources.filter(t => t.id !== resid)
            this.getPublic()
          } else {
            alert('')
          }
        }).catch((response) => {
          alert('')
        })
      }
    },
    lookRatee (res) {
      this.isShow = true
      this.resRateid = res
    },
    orderedOne (res) {
      if (confirm('is ordered')) {
        this.$http.post(window.baseUrl + '/api/order/create/', {
          'data': [res]
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getPublic()
            } else {
              alert('')
            }
          } else {
            alert('')
          }
        }).catch((response) => {
          alert('')
        })
      }
    }
  }
}
</script>



