<template>
    <div id="outermain">
    	<div class="container">
            <div class="row">
                <!-- <section id="maincontent" class="ten columns positionleft"> -->
                <section id="maincontent" >
                	<div class="padcontent">
                
                    	<section class="content">
                        
                            <div class="breadcrumb">Home / <a></a></div>
                            <h1 class="pagetitle"></h1>

                            <div class="table_block" id="order-detail-content">
                                <table id="cart_summary">
                                    <thead>
                                        <tr>
                                            <th class="cart_product first_item"><a :class="{button: true, exclusive: false}" id='btn' v-if="show" style='float:right; margin-right:10px'  @click="deleteOrder"></a></th>
                                            <th class="cart_product first_item"></th>
                                            <th class="cart_product first_item"></th>
                                            <th class="cart_description item"></th>
                                            <th class="cart_ref item">.</th>
                                            <th class="cart_avail item"></th>
                                            <th class="cart_unit item"></th>
                                            <th class="cart_total item"></th>
                                        </tr>
                                    </thead>

                                    <tbody>
                                        <tr v-for="order in orders" class="cart_item" >
                                            <td> <input type="checkbox" v-model='num' id="payment_method_paypal" class="input-radio" name="payment_method" :value="order.id"  /></td>
                                            <td class="cart_product"><router-link :to="'/product-details/' + order.resource.id"><img :src='order.resource.resource_picture' style="height:37px;width:50px"></router-link></td>

                                            <td class="cart_product">
                                                {{ order.order_number }}
                                            </td>
                                            <td class="cart_description">
                                               <router-link :to="'/product-details/' + order.resource.id">{{ order.resource.name }} </router-link>	
                                            </td>
                                            <td class="cart_ref" v-if="order.process === 1"></td>
                                            <td class="cart_ref" v-if="order.process === 2"> </td>
                                            <td class="cart_ref" v-if="order.process === 3"> </td>
                                            <td class="cart_ref" v-if="order.process === 4"> </td>
                                            <td class="cart_ref" v-if="order.process === 5"> </td>
                                            <td class="cart_avail">{{ order.community.name }}</td>
                                            <td class="cart_unit">{{ order.resource.intergration }}</td>
                                            <td class="cart_total">
                                                <span id='operation' v-if="order.process === 1" @click='useOrder(order.id)'></span>
                                                <span id='operation' v-if="order.process === 3" @click='returnOrder(order.id)'></span>
                                                <span id='operation' v-if="order.process === 4 || order.process === 2 || order.process === 5" @click='deleteOrderSingle(order.id)'></span>
                                                <span id='operation' v-if="order.process !== 2 && order.process !== 4 && order.process !== 5" @click='cancelOrder(order.id)'></span>
                                                <router-link v-if="order.process === 4" :to="'/Ratee/' + order.resource.id" class="more"></router-link>
                                            </td>
                                        </tr>
                                    </tbody>

                                    <tfoot>
                                        <tr class="cart_total price">
                                            <td colspan="2" class="none1"></td>
                                            <td class="none2">  </td>
                                            <td class="none2">  </td>
                                            <td class="none3"> <div v-on:click='getPrevious()' id='operation' style="float:right"> </div></td>
                                            <td colspan="2"><div v-on:click='getNext()' id='operation' style="float:left"> </div></td>
                                            <td id="total_product" class="price"></td>
                                        </tr>
                                        <tr class="cart_total price">
                                        	<td colspan="2" class="none1"> </td>
                                            <td class="none2"> </td>
                                            <td class="none3"> </td>
                                            <td class="none2"> </td>
                                            <td colspan="2"></td>
                                            <td id="total_product" class="price">{{ resourcenNum }}</td>
                                        </tr>
                                        <tr class="cart_total delivery">
                                        	<td colspan="2" class="none1"> </td>
                                            <td class="none2"> </td>
                                            <td class="none2">  </td>
                                            <td class="none3"> </td>
                                            <td colspan="2"  @click="getMyOrders()"></td>
                                            <td id="total_shipping" class="price">{{ sources }}</td>
                                        </tr>								
                                    </tfoot>
                                    </table>
                            </div>
							
         <!--                    <a class="button" style='float:right;  margin-right:50px' id='btn' ></a>&nbsp;
                            <a :class="{button: true, exclusive: false}" id='btn' style='float:right; margin-right:10px'></a> -->
                             
                            <br><br><br>


                            <button>&nbsp
                                <button>
                                    <div id="payment">
                                        <ul class="payment_methods">
                                            <li>
                                                <label for="payment_method_bacs"> </label> 
                                                <div class="payment_box">
                                                    <p></p>
                                                </div>						
                                            </li>
                                            <li>
                                                <label for="payment_method_cheque"> </label> 	
                                                <div class="payment_box">
                                                    <p>.</p>
                                                </div>  				
                                            </li>
                                            <li>
                                                <label for="payment_method_paypal"></label>
                                                <div class="payment_box">
                                                    <p></p>
                                                </div>   					
                                            </li>
                                        </ul>
                                    
                                        <div class="form-row">		
                                            <a href="#" class="button">Place order</a>
                                        </div>
                                        <div class="clear"></div>
                                    </div>
                                </button>
                            </button>
						          </section>
                    </div>
                </section>
                
            </div>
        </div>
    </div>
</template>
<style>
#operation:hover{ 
color:red
}

#btn:hover{ 
background-color:red
}
.modal-scale-transition{
  transition: transform,opacity .3s ease;
}

.modal-scale-enter,
.modal-scale-leave {
    opacity: 0;
}

.modal-scale-enter {
  transform: scale(1.1);
}
.modal-scale-leave {
  transform: scale(0.8);
}
</style>
<script>
export default {
  name: 'Checkout',
  data () {
    return {
      resourceId: '',
      orders: '',
      sources: 0,
      resourcenNum: 0,
      showing: false,
      num: [],
      pagePrevious: '',
      pageNext: '',
      show: false
    }
  },
  created () {
    this.getMyOrders()
  },
  watch: {
    num (curVal, oldVal) {
      if (curVal) {
        if (this.num.length >= 0) {
          this.show = true
          this.sources = 0
          this.resourcenNum = this.num.length
          if (this.num.length === 0) {
            this.sources = 0
            this.show = false
          } else {
            for (var i = 0; i < this.num.length; i++) {
              var a = this.orders.filter(k => k.id === this.num[i])
              this.sources += a[0].intergration
            }
          }
        }
      }
    }
  },
  methods: {
    getCartList () {
      this.$http.get(window.baseUrl + '/api/accouasdnts/get_cart_list/').then((response) => {
        if (response.data.code === 200) {
          this.resources = response.data.data
        } else {
          console.log(456)
        }
      }).catch((response) => {
        console.log(55)
      })
    },
    getMyOrders () {
      this.$http.get(window.baseUrl + '/api/order/get_my_orders/').then((response) => {
        if (response.data.code === 200) {
          this.orders = response.data.data
          this.pageNext = response.data.next
          this.pagePrevious = response.data.previous
        } else {
          console.log(456)
        }
      }).catch((response) => {
        console.log(789)
      })
    },
    getNext () {
      if (this.pageNext === null) {
        alert('')
      } else {
        this.$http.get(this.pageNext).then((response) => {
          if (response.data.code === 200) {
            this.orders = response.data.data
            this.pageNext = response.data.next
            this.pagePrevious = response.data.previous
          } else {
            alert('')
          }
        }).catch((response) => {
          alert('')
        })
      }
    },
    getPrevious () {
      if (this.pagePrevious === null) {
        alert('')
      } else {
        this.$http.get(this.pagePrevious).then((response) => {
          if (response.data.code === 200) {
            this.orders = response.data.data
            this.pageNext = response.data.next
            this.pagePrevious = response.data.previous
          } else {
            alert('')
          }
        }).catch((response) => {
          alert('')
        })
      }
    },
    deleteOrder () {
      if (confirm('?')) {
        this.$http.post(window.baseUrl + '/api/order/delete/', {
          'pk_list': this.num
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getMyOrders()
              this.sources = 0
              this.resourcenNum = 0
            } else {
              alert('1')
            }
          } else {
            alert('2')
          }
        }).catch((response) => {
          alert('3')
        })
      }
    },
    deleteOrderSingle (orderid) {
      if (confirm('?')) {
        this.$http.post(window.baseUrl + '/api/order/delete/', {
          'pk_list': [orderid]
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getMyOrders()
            } else {
              alert('1')
            }
          } else {
            alert('2')
          }
        }).catch((response) => {
          alert('3')
        })
      }
    },
    cancelOrder (orderid) {
      if (confirm('?')) {
        this.$http.post(window.baseUrl + '/api/order/cancel/', {
          'order_id': orderid
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getMyOrders()
            } else {
              alert(response.data.msg)
            }
          } else {
            alert('2')
          }
        }).catch((response) => {
          alert('3')
        })
      }
    },
    useOrder (orderid) {
      if (confirm('?')) {
        this.$http.post(window.baseUrl + '/api/order/order_operation/', {
          'order_id': orderid,
          'process': 3
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getMyOrders()
            } else {
              alert(response.data.msg)
            }
          } else {
            alert('2')
          }
        }).catch((response) => {
          alert('3')
        })
      }
    },
    returnOrder (orderid) {
      if (confirm('?')) {
        this.$http.post(window.baseUrl + '/api/order/return_back/', {
          'order_id': orderid
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getMyOrders()
            } else {
              alert(response.data.msg)
            }
          } else {
            alert('2')
          }
        }).catch((response) => {
          alert('3')
        })
      }
    }
  }
}
</script>



