<template>
    <div id="outermain">
    	<div class="container">
            <div class="row">
                <!-- <section id="maincontent" class="ten columns positionleft"> -->
                <section id="maincontent" >
                	<div class="padcontent">
                
                    	<section class="content">
                        
                            <div class="breadcrumb"> Home / <a>></a></div>
                            <h1 class="pagetitle"></h1>
                          
                            <div class="table_block" id="order-detail-content">
                                <table id="cart_summary">
                                    <thead>
                                        <tr>
                                            <th class="cart_product first_item"><a class="button" v-if="show" id='btn' @click='remove_from_the_cart_all()'></a></th>
                                            <th class="cart_product"></th>
                                            <th class="cart_product first_item"></th>
                                            <th class="cart_description item"></th>
                                            <th class="cart_avail item"></th>
                                            <th class="cart_unit item"></th>
                                            <th class="cart_total item"></th>
                                        </tr>
                                    </thead>

                                    <tbody>
                                        <tr v-for="resource in resources" class="cart_item" >
                                            <td> <input type="checkbox" v-model="num" id="payment_method_paypal" class="input-radio" name="payment_method" :value="resource"/></td>
                                            <td class="cart_product"><router-link :to="'/product-details/' + resource.id"><img :src='resource.resource_picture' style="height:40px;width:50px"></router-link></td>
                                            <td class="cart_product">
                                                <router-link :to="'/product-details/' + resource.id">{{resource.name}}</router-link>
                                            </td>
                                            <td class="cart_description">
                                               {{ resource.pub_address }}   	
                                            </td>
                                            
                                            <td class="cart_avail">{{ resource.community.name }}</td>
                                            <td class="cart_unit">{{ resource.intergration }}</td>
                                            <td class="cart_total">
                                                <span class="price" @click='orderedOne(resource)' id='operation'></span>
                                                <span class="price" @click='remove_from_the_cart(resource.id)' id='operation'></span>
                                            </td>
                                        </tr>
                                        
                                    </tbody>
                                    
                                    <tfoot>
                                        <tr class="cart_total price">
                                        	<td colspan="2" class="none1"> </td>
                                            <td class="none2"> </td>
                                            <td class="none3"> </td>
                                            <td colspan="2"></td>
                                            <td id="total_product" class="price">{{ resourcenNum }}</td>
                                        </tr>
                                        <tr class="cart_total delivery">
                                        	<td colspan="2" class="none1"> </td>
                                            <td class="none2"> </td>
                                            <td class="none3"> </td>
                                            <td colspan="2"></td>
                                            <td id="total_shipping" class="price">{{ sources }}</td>
                                        </tr>								
                              <!-- 
                                        <tr class="cart_total_price">
                                            <td class="cart_voucher" id="cart_voucher" colspan="7">
                                            <form id="voucher" method="post" action="#">
                                              <fieldset>
                                                  <h3><label>Vouchers</label></h3>
                                                  <p>
                                                  <label>code: </label>
                                                  <input type="text" value="" name="discount_name" class="discount_name">
                                                  <input type="hidden" name="submitDiscount"><input type="submit" class="button" value="Add Code" name="submitAddDiscount">
                                                  </p>
                                              </fieldset>
                                            </form>
                                            </td>
                                        </tr> -->
                                    </tfoot>
                                    </table>
                            </div>
							
                            <!-- <a class="button" style='float:right;  margin-right:50px' id='btn' ></a>&nbsp; -->
                            <a class="button" style='float:right; margin-right:10px' id='btn'  @click='ordered()'></a>
                            <a class="button" style='float:right; margin-right:10px'  id='btn' @click='remove_from_the_cart_all()'></a>
                            <br><br><br>
                              <button>&nbsp
                                <button>
                                    <div id="payment">
                                        <ul class="payment_methods">
                                            <li>
                                                <label for="payment_method_bacs"> </label> 
                                                <div class="payment_box">
                                                    <p></p>
                                                </div>						
                                            </li>
                                            <li>
                                                <label for="payment_method_cheque"> </label> 	
                                                <div class="payment_box">
                                                    <p>.</p>
                                                </div>  				
                                            </li>
                                            <li>
                                                <label for="payment_method_paypal"></label>
                                                <div class="payment_box">
                                                    <p></p>
                                                </div>   					
                                            </li>
                                        </ul>
                                    
                                        <div class="form-row">		
                                            <a href="#" class="button">Place order</a>
                                        </div>
                                        <div class="clear"></div>
                                    </div>
                                </button>
                            </button>
						</section>
                     
                    </div>
                </section>
                
            </div>
        </div>
    </div>
</template>
<style>
#operation:hover{ 
color:red
}

#btn:hover{ 
background-color:red
}
.modal-scale-transition{
  transition: transform,opacity .3s ease;
}

.modal-scale-enter,
.modal-scale-leave {
    opacity: 0;
}

.modal-scale-enter {
  transform: scale(1.1);
}
.modal-scale-leave {
  transform: scale(0.8);
}
</style>
<script>
export default {
  name: 'Checkout',
  data () {
    return {
      resourceId: '',
      resources: '',
      sources: 0,
      resourcenNum: 0,
      num: [],
      show: false
    }
  },
  created () {
    this.getCartList()
  },
  watch: {
    num (curVal, oldVal) {
      if (curVal) {
        if (this.num.length >= 0) {
          this.show = true
          this.sources = 0
          this.resourcenNum = this.num.length
          if (this.num.length === 0) {
            this.show = false
            this.sources = 0
          } else {
            for (var i = 0; i < this.num.length; i++) {
              this.sources += this.num[i].intergration
            }
          }
        }
      }
    }
  },
  methods: {
    getCartList () {
      this.$http.get(window.baseUrl + '/api/accounts/get_cart_list/').then((response) => {
        if (response.data.code === 200) {
          this.resources = response.data.data
        } else {
          console.log(456)
        }
      }).catch((response) => {
        console.log(55)
      })
    },
    ordered () {
      this.$http.post(window.baseUrl + '/api/order/create/', {
        'data': this.num
      }).then((response) => {
        if (response.data.code === 200) {
          if (response.data.status === 1) {
            this.getCartList()
          } else {
            alert('')
          }
        } else {
          alert('')
        }
      }).catch((response) => {
        alert('')
      })
    },
    remove_from_the_cart_all () {
      if (this.num.length === 0) {
        alert('no')
      }
      if (confirm('is delete') && this.num.length > 0) {
        var reList = []
        for (var i = 0; i < this.num.length; i++) {
          reList.push(this.num[i].id)
        }
        this.$http.post(window.baseUrl + '/api/accounts/remove_cart/', {'res_list': reList}).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              // this.resources = this.resources.filter(t => t.id !== resid)
              this.getCartList()
            } else {
              alert('')
            }
          } else {
            alert('')
          }
        }).catch((response) => {
          alert('')
        })
      }
    },
    remove_from_the_cart (resid) {
      if (confirm('is delete')) {
        this.$http.post(window.baseUrl + '/api/accounts/remove_cart/', {'res_list': [resid]}).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              // this.resources = this.resources.filter(t => t.id !== resid)
              this.getCartList()
            } else {
              alert('')
            }
          } else {
            alert('')
          }
        }).catch((response) => {
          alert('')
        })
      }
    },
    orderedOne (res) {
      if (confirm('is ordered')) {
        this.$http.post(window.baseUrl + '/api/order/create/', {
          'data': [res]
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getCartList()
            } else {
              alert('')
            }
          } else {
            alert('')
          }
        }).catch((response) => {
          alert('')
        })
      }
    }
  }
}
</script>



